import React from 'react';
import {IApplicationForm, IInputRadioGroupData, ITShirtOptionType} from "../../constants/interfaces";
import Typography from "@material-ui/core/Typography";
import {
    ExpansionPanel,
    ExpansionPanelDetails,
    ExpansionPanelSummary,
    FormControl,
    FormControlLabel,
    FormLabel,
    Grid,
    Icon,
    Radio,
    RadioGroup
} from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import InputAutoCompleteForCountry from "../forms/InputAutoCompleteForCountry";
import {countries, tShirtOptions} from "../../constants";
import InputPhone from "../forms/InputPhone";
import Autocomplete from "@material-ui/lab/Autocomplete";
import InputRadioGroup from "../forms/InputRadioGroup";
import {
    degreeOptions,
    dietaryRestrictionOptions,
    disabilityOptions,
    informationSourceOptions
} from "../../constants/options";
import InputTextField from "../forms/InputTextField";
import InputCheckboxGroup from "../forms/InputCheckboxGroup";
import InputUploadMedia, {AcceptedFileTypes, ExampleTypeOptions} from "../forms/InputUploadMedia";

interface IViewApplicationForm {
    applicationForm: IApplicationForm,
    loading: boolean,
}

const ViewApplicationForm: React.FC<IViewApplicationForm> = ({applicationForm, loading = true}) => {

    if (loading) return <></>;

    const PersonalDetails = () => {
        return <>
            <ExpansionPanel>
                <ExpansionPanelSummary
                    expandIcon={<Icon>expand_more_icon</Icon>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography variant={"h6"}>Personal Details (Section 1)</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <Grid item>
                        <div>
                            <TextField
                                variant="outlined"
                                margin="normal"
                                required
                                fullWidth
                                label="Full name"
                                id="fullName"
                                name="fullName"
                                autoComplete="fullName"
                                value={applicationForm.personalDetails.fullName}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                fullWidth
                                label="Known as (if different)"
                                id="nickName"
                                name="nickName"
                                autoComplete="nickName"
                                value={applicationForm.personalDetails.nickName}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                required
                                fullWidth
                                label="Address (please detail)"
                                id="address"
                                name="address"
                                autoComplete="address"
                                multiline={true}
                                value={applicationForm.personalDetails.address}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                fullWidth
                                required
                                label="City"
                                id="city"
                                name="city"
                                autoComplete="city"
                                value={applicationForm.personalDetails.city}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                fullWidth
                                required
                                label="State/Province"
                                id="province"
                                name="province"
                                autoComplete="province"
                                value={applicationForm.personalDetails.province}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                fullWidth
                                required
                                label="Postcode"
                                id="postcode"
                                name="postcode"
                                autoComplete="postcode"
                                value={applicationForm.personalDetails.postcode}
                            />


                            <InputAutoCompleteForCountry
                                label={"Country"}
                                value={applicationForm.personalDetails.country}
                            />

                            <InputPhone
                                required
                                labels={'Mobile Phone number (please include the country code)'}
                                value={applicationForm.personalDetails.mobilePhone ?? ''}
                            />
                            <InputPhone
                                labels={'Phone number (please include the country code)'}
                                value={applicationForm.personalDetails.phone ?? ''}
                            />
                            <InputPhone
                                labels={'WhatsApp number (please include the country code)'}
                                value={applicationForm.personalDetails.waNumber ?? ''}
                            />

                            <TextField
                                variant="outlined"
                                margin="normal"
                                fullWidth
                                label="Facebook (if any)"
                                id="facebook"
                                name="facebook"
                                autoComplete="facebook"
                                value={applicationForm.personalDetails.facebook ?? ''}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                fullWidth
                                label="Instagram (if any)"
                                id="instagram"
                                name="instagram"
                                autoComplete="instagram"
                                value={applicationForm.personalDetails.instagram ?? ''}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                fullWidth
                                label="Another social media"
                                id="socialMedia"
                                name="socialMedia"
                                autoComplete="socialMedia"
                                value={applicationForm.personalDetails.socialMedia ?? ''}
                            />
                            <Autocomplete
                                id="T-Shirt-Size"
                                options={tShirtOptions as ITShirtOptionType[]}
                                getOptionLabel={option => {
                                    // e.g value selected with enter, right from the input
                                    if (typeof option === 'string') {
                                        return option;
                                    }
                                    if (option.inputValue) {
                                        return option.inputValue;
                                    }
                                    return option.size;
                                }}
                                renderOption={option => option.size}
                                style={{width: '100%', marginTop: 16, marginBottom: 8}}
                                freeSolo
                                disableClearable={true}
                                value={{size: applicationForm.personalDetails.tShirtSize ?? ''}}
                                renderInput={params => (
                                    <TextField
                                        {...params}
                                        required
                                        fullWidth
                                        label="T-Shirt (Asian size)" variant="outlined"/>
                                )}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                fullWidth
                                required
                                id="date-of-birth"
                                label="Date of birth"
                                type="date"
                                value={applicationForm.personalDetails.dateOfBirth?.toISOString().split('T')[0] ?? ''}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                            />
                            <FormControl
                                component="fieldset"
                                style={{width: '100%', marginTop: 16, marginBottom: 8, textAlign: 'left'}}
                            >
                                <FormLabel component="legend">Gender</FormLabel>
                                <RadioGroup
                                    aria-label="sex"
                                    name="sex"
                                    value={applicationForm.personalDetails.sex}
                                >
                                    <FormControlLabel value="female" control={<Radio/>} label="Female"/>
                                    <FormControlLabel value="male" control={<Radio/>} label="Male"/>
                                </RadioGroup>
                            </FormControl>

                            <FormControl
                                component="fieldset"
                                style={{width: '100%', marginTop: 16, marginBottom: 8, textAlign: 'left'}}
                            >
                                <FormLabel component="legend">Marital status</FormLabel>
                                <RadioGroup
                                    aria-label="maritalStatus"
                                    name="maritalStatus"
                                    value={applicationForm.personalDetails.maritalStatus}
                                >
                                    <FormControlLabel value="single" control={<Radio/>} label="Single"/>
                                    <FormControlLabel value="married" control={<Radio/>} label="Married"/>
                                </RadioGroup>
                            </FormControl>
                            <TextField
                                variant="outlined"
                                margin="normal"
                                fullWidth
                                label="Religion (if any)"
                                id="religion"
                                name="religion"
                                autoComplete="religion"
                                value={applicationForm.personalDetails.religion ?? ''}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                required
                                fullWidth
                                label="Passport number"
                                id="passportNumber"
                                name="passportNumber"
                                autoComplete="passportNumber"
                                value={applicationForm.personalDetails.passportNumber ?? ''}
                            />
                            <TextField
                                variant="outlined"
                                margin="normal"
                                fullWidth
                                required
                                id="expiry-date"
                                label="Expiry date (Passport)"
                                type="date"
                                value={applicationForm.personalDetails.expiryDate?.toISOString().split('T')[0] ?? ''}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                            />
                            <InputAutoCompleteForCountry
                                label={"Nationality"}
                                value={applicationForm.personalDetails.nationality ?? countries[99].label}
                            />
                        </div>
                    </Grid>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        </>;
    };

    const EducationBackground = () => {
        return <>
            <ExpansionPanel>
                <ExpansionPanelSummary
                    expandIcon={<Icon>expand_more_icon</Icon>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography variant={"h6"}>Education Background (Section 2)</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <Grid item>
                        <InputRadioGroup
                            options={degreeOptions.map((data) => {
                                return {
                                    label: data,
                                    value: data,
                                } as IInputRadioGroupData;
                            })}
                            label={'Latest degree'}
                            id={'latestDegree'}
                            value={applicationForm.educationBackground?.latestDegree ?? degreeOptions[0]}
                            required={true}
                        />
                        <InputTextField
                            value={applicationForm.educationBackground?.homeUniversity}
                            id={'homeUniversity'}
                            label={"Home University / Institution / Affiliation"}
                            required={true}
                        />
                        <InputTextField
                            value={applicationForm.educationBackground?.faculty}
                            id={'faculty'}
                            label={"Faculty / Division"}
                        />
                        <InputTextField
                            value={applicationForm.educationBackground?.department}
                            id={'department'}
                            label={"Department / Position"}
                        />
                        <InputAutoCompleteForCountry
                            label={"Country"}
                            value={applicationForm.educationBackground?.country ?? countries[99].label}
                        />
                    </Grid>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        </>;
    };

    const HealthCondition = () => {
        return <>
            <ExpansionPanel>
                <ExpansionPanelSummary
                    expandIcon={<Icon>expand_more_icon</Icon>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography variant={"h6"}>Dietary and Health Condition (Section 3)</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <Grid item>
                        <InputCheckboxGroup
                            label={'Dietary Restriction'}
                            options={dietaryRestrictionOptions}
                            values={applicationForm.healthCondition?.dietaryRestriction ?? []}
                            required={true}
                        />
                        <InputTextField
                            value={applicationForm.healthCondition?.dietaryDetail}
                            id={'dietaryDetail'}
                            label={"For other or specific dietary, please specify here"}
                            multiline={true}
                        />
                        <InputRadioGroup
                            options={disabilityOptions.map((data) => {
                                return {
                                    label: data,
                                    value: data,
                                } as IInputRadioGroupData;
                            })}
                            label={'Do you have any disability?'}
                            id={'disabilitylatestDegree'}
                            value={applicationForm.healthCondition?.disability ?? disabilityOptions[1]}
                            required={true}
                        />
                        <InputTextField
                            value={applicationForm.healthCondition?.disabilityDetail}
                            id={'disabilityDetail'}
                            label={"If you have any disabilities, please specify here"}
                            multiline={true}
                        />
                        <InputTextField
                            value={applicationForm.healthCondition?.medicalCondition}
                            id={'medicalCondition'}
                            label={"If you have any medical condition to be aware of, please specify here"}
                            // helperText={' from and any medication taken. (if any)'}
                            multiline={true}
                        />
                        <InputTextField
                            value={applicationForm.healthCondition?.specialRequirements}
                            id={'specialRequirements'}
                            label={"If you have any allergies or sensitivity, please specify here"}
                            // helperText={"Any issues we should be aware of"}
                            multiline={true}
                        />
                    </Grid>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        </>;
    };

    const PersonalStatement = () => {
        return <>
            <ExpansionPanel>
                <ExpansionPanelSummary
                    expandIcon={<Icon>expand_more_icon</Icon>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography variant={"h6"}>Personal Statement (Section 4)</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <Grid item>
                        <InputTextField
                            value={applicationForm.personalStatement?.motivation}
                            id={'motivation'}
                            label={"Please tell us your motivation for applying *"}
                            // helperText={"What knowledge you seek to gain, and how UM iCamp program would benefit from your attendance (max. 500 words)*"}
                            helperText={"max. 500 words"}
                            multiline={true}
                            // required={true}
                            rows={3}
                            rowsMax={8}
                        />
                        <InputTextField
                            value={applicationForm.personalStatement?.personalExperiences}
                            id={'personalExperiences'}
                            label={"Please share your volunteering or extra curricular experience in school or your community *"}
                            // helperText={" As it relates to the previous short essay (max. 500 words)*"}
                            helperText={"max. 500 words"}
                            multiline={true}
                            // required={true}
                            rows={3}
                            rowsMax={8}
                        />
                        <InputTextField
                            value={applicationForm.personalStatement?.specialitiesDescribe}
                            id={'specialitiesDescribe'}
                            label={"Your achievements, traits, uniqueness, or specialities (why we need to choose you as a participant)"}
                            // helperText={"That makes you the most suitable candidate for the program"}
                            multiline={true}
                            required={true}
                            rows={3}
                            rowsMax={8}
                        />
                        <InputTextField
                            value={applicationForm.personalStatement?.afterProgram}
                            id={'afterProgram'}
                            label={"What is your plan after participating in this program?"}
                            multiline={true}
                            required={true}
                            rows={3}
                            rowsMax={8}
                        />
                        <InputCheckboxGroup
                            label={'How did you hear about UM iCamp 2020?'}
                            options={informationSourceOptions}
                            values={applicationForm.personalStatement?.informationSources ?? []}
                            required={true}
                        />
                        <InputTextField
                            value={applicationForm.personalStatement?.additionalInformationSources}
                            id={'additionalInformationSources'}
                            label={"Please input here"}
                        />
                    </Grid>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        </>;
    };

    const EmergencyContact = () => {
        return <>
            <ExpansionPanel>
                <ExpansionPanelSummary
                    expandIcon={<Icon>expand_more_icon</Icon>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography variant={"h6"}>Emergency Contact Detail (Section 5)</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <Grid item>
                        <InputTextField
                            value={applicationForm.emergencyContact?.fullName}
                            id={'fullName'}
                            label={"Full name"}
                            required={true}
                        />
                        <InputTextField
                            value={applicationForm.emergencyContact?.relationship}
                            id={'relationship'}
                            label={"Relationship to participant"}
                            required={true}
                        />
                        <InputTextField
                            value={applicationForm.emergencyContact?.email}
                            id={'email'}
                            label={"E-mail"}
                            required={true}
                            type={'email'}
                        />
                        <InputPhone
                            required
                            labels={'Phone or WhatsApp number (please include the country code)'}
                            value={applicationForm.emergencyContact?.phone ?? ''}
                        />
                        <br/>
                        <InputTextField
                            value={applicationForm.emergencyContact?.address}
                            id={'address'}
                            label={"Address"}
                            required={true}
                            multiline={true}
                        />
                        <InputTextField
                            value={applicationForm.emergencyContact?.city}
                            id={'city'}
                            label={"City"}
                            required={true}
                            multiline={true}
                        />
                        <InputTextField
                            value={applicationForm.emergencyContact?.province}
                            id={'province'}
                            label={"Province"}
                            required={true}
                        />
                        <InputTextField
                            value={applicationForm.emergencyContact?.postcode}
                            id={'postcode'}
                            label={"Postcode"}
                            required={true}
                        />
                        <InputAutoCompleteForCountry
                            label={"Country"}
                            value={applicationForm.emergencyContact?.country ?? countries[99].label}
                        />
                    </Grid>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        </>;
    };

    const ApplicantMedia = () => {
        return <>
            <ExpansionPanel>
                <ExpansionPanelSummary
                    expandIcon={<Icon>expand_more_icon</Icon>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography variant={"h6"}>Applicant Media (Section 6)</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <Grid container justify={"center"}>
                        <Grid item xs={12} md={6}>
                            <InputUploadMedia
                                disabled={true}
                                dialogTitle={"Passport size photo *"}
                                subHeader={'(4x6 cm)'}
                                fileName={'passport-photo'}
                                value={applicationForm.applicantMedia?.passportPhoto}
                                showExampleButton={true}
                                exampleType={ExampleTypeOptions.ASSETS_IMAGE}
                                exampleData={'apply/assets/images/passport-photo-example.png'}
                            />
                            <InputUploadMedia
                                disabled={true}
                                dialogTitle={"Scan of passport *"}
                                fileName={'passport-scan'}
                                value={applicationForm.applicantMedia?.passportScan}
                                showExampleButton={true}
                                exampleType={ExampleTypeOptions.ASSETS_IMAGE}
                                exampleData={'apply/assets/images/passport-scan-example.png'}
                            />
                            <InputUploadMedia
                                disabled={true}
                                dialogTitle={"Medical certificate *"}
                                fileName={'medical-certificate'}
                                value={applicationForm.applicantMedia?.medicalCertificate}
                                acceptedFileType={AcceptedFileTypes.PDF}
                            />
                            <InputUploadMedia
                                disabled={true}
                                dialogTitle={"Declaration letter *"}
                                fileName={'declaration-letter'}
                                value={applicationForm.applicantMedia?.declarationLetter}
                                acceptedFileType={AcceptedFileTypes.PDF}
                                showExampleButton={true}
                                exampleType={ExampleTypeOptions.LINK}
                                exampleData={'https://drive.google.com/file/d/1XY4gbHkBv-VbjLsIJ5AiwmBWOk8QXZqH/view'}
                            />
                        </Grid>
                    </Grid>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        </>;
    };


    return (<div>
        <PersonalDetails/>
        <EducationBackground/>
        <HealthCondition/>
        <PersonalStatement/>
        <EmergencyContact/>
        <ApplicantMedia/>
    </div>);
};

export default ViewApplicationForm;